<template>
  <div id="app">
    <div class="container">

      <p><strong>日期格式化</strong></p>
      <p><vueDateFormat :format="format1" :time="time1"></vueDateFormat></p>
      <pre><code>
    &lt;vueDateFormat :format=&quot;format&quot; :time=&quot;time&quot;&gt;&lt;/vueDateFormat&gt;

    data () {
      return {
        format:'yyyy年MM月dd日 hh:mm:ss',
        time:new Date()
      }
    }
      </code></pre>




      <p><strong>日期格式化（自动更新）</strong></p>
      <p><vueDateFormat :format="format2" :autoUpdate="autoUpdate2" :autoTime="autoTime2"></vueDateFormat></p>
      <pre><code>
    &lt;vueDateFormat :format=&quot;format&quot; :autoUpdate=&quot;autoUpdate&quot; :autoTime=&quot;autoTime&quot;&gt;&lt;/vueDateFormat&gt;
    
    data () {
      return {
        format:'yyyy/MM/dd hh:mm:ss',
        autoUpdate:true,
        autoTime:1
      }
    }
      </code></pre>




      <p><strong>timeago</strong></p>
      <p><vueDateFormat :time="time3" :type="type3"></vueDateFormat></p>
      <pre><code>
    &lt;vueDateFormat :time=&quot;time&quot; :type=&quot;type&quot;&gt;&lt;/vueDateFormat&gt;

    data () {
      return {
        type:'ago',
        time:'2018-01-01'
      }
    }
      </code></pre>




      <p><strong>timeago（自动更新）</strong></p>
      <p><vueDateFormat :time="time4" :type="type4" :autoUpdate="autoUpdate4" :autoTime="autoTime4"></vueDateFormat></p>
      <pre><code>
    &lt;vueDateFormat :time=&quot;time&quot; :type=&quot;type&quot; :autoUpdate=&quot;autoUpdate&quot; :autoTime=&quot;autoTime&quot;&gt;&lt;/vueDateFormat&gt;


    /**30秒后会看出自动更新的效果，30秒前都会显示‘刚刚’**/
    data () {
      return {
        type:'ago',
        time:new Date(),
        autoUpdate:true,
        autoTime:1
      }
    }
      </code></pre>
    </div>
  </div>
</template>

<script>
import 'milligram/dist/milligram.min.css'

export default {
  name: 'App',
  data () {
    return {
      format1:'yyyy年MM月dd日 hh:mm:ss',
      time1:new Date(),
      format2:'yyyy/MM/dd hh:mm:ss',
      autoUpdate2:true,
      autoTime2:1,
      type3:'ago',
      time3:'2018-01-01',
      type4:'ago',
      time4:new Date(),
      autoUpdate4:true,
      autoTime4:1
    }
  }
}
</script>

<style>

</style>



